import React, { useState, useEffect } from 'react';
import {NavLink} from 'react-router-dom';
import '../../Ind.css';
import axios from 'axios';
import * as API from '../../constants/Config';

const HomePage = () => {

    const [job, setJob] = useState([]);
    const [isLoading, setIsLoading] = useState(false);

    useEffect(() => {
        const getJob = async () => {
            try {
                const payload = await axios.get(`${API.API}${API.RECRUITMENT_NEWS}`);
                setJob(payload.data);
                return payload.data;
            } catch (error) {
                console.log(error);
            }
            
        }
        getJob();
    }, []);

    // const getOrg = async (id) => {
    //     console.log(`${API.API}${API.ORGANIZATION}/${id}`);
    //     const payload = await axios.get(`${API.API}${API.ORGANIZATION}/${id}`)
    //     return payload.data;
    // }

    const handleGetJob = async (link) => {
        try {
            setIsLoading(true);
            const payload = await axios.get(link);
            setJob(payload.data);
            setTimeout(() => {
                setIsLoading(false);
            }, 1000);
            return payload.data;
        } catch (error) {
            console.log(error);
        }
    }

    const convertDate = t => {
        t = t.split(/[- :]/);
        var d = new Date(Date.UTC(t[0], t[1] - 1, t[2], t[3], t[4], t[5]));
        return d.toLocaleDateString();
    };

    const getDiffDate = (date1, date2) => {
        const diffTime = date2 - date1;
        const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
        return diffDays;
    };

    const getTimeLeft = (datetime) => {
        const d = new Date();
        const currentDate = d.toLocaleDateString();
        const interviewTime = convertDate(datetime);
        const ld = getDiffDate(new Date(currentDate), new Date(interviewTime));
        return ld > 1
                ? ld + ' days left'
                : ld === 1
                ? ld + ' day left'
                : 'Expired'        
    }

    return (    
    <div>
        <div className="container-fluid">
            <div className="introduce-content container">
                <div className="title-ic">
                    Tn trng l thnh cng
                </div>
                <div className="ic-content">
                    Con ngi l mt trong nhng yu t quan trng quyt nh s thnh bi ca mt doanh nghip. Thu hiu su sc iu ny, MPHR cam kt cung cp cho khch hng ca mnh nhng gii php nhn s chuyn bit v hiu qu nht nhm p ng nhu cu a dng ca
                    khch hng trong nc v quc t v Nhn S - o To.
                    <br />
                    <br/> Da trn nn tng Minh Phc Telecom - nh cung cp dch v hng u v gii php Contact Center, BPO vi 15 nm kinh nghim ti Vit Nam. MPHR t ho theo ui cc gi tr ton vn trn tt c cc dch v ca mnh, gip khch
                    hng gii quyt tt c cc vn  phc tp nht v Nhn s bao gm:
                    <br />
                    <br/> - Dch v Qun l tin lng <br/> - Dch v Cho thu nhn s <br/> - Dch v Tuyn dng nhn s <br/> - Dch v o to <br/> - Dch v T vn hng nghip
                    <br/>
                    <br/>
                    <h4>Gi tr ct li</h4>
                    <br/> Tn trng  Thch ng  Hiu qu
                    <br/>
                    <br/>
                    <h4>S mnh</h4>
                    <br/> Thu hiu c rng "Con ngi l ti sn qu gi nht ca doanh nghip", MPHR lun trung thnh vi s mnh cung ng ngun lc tt nht cho cc doanh nghip  nhiu lnh vc.
                    <br/>
                    <br/>
                    <h4>Tm nhn</h4>
                    <br/> Tr thnh cng ty s mt ti Vit Nam cung cp cc dch v v gii php nhn s cho khch hng.
                </div>
            </div>
        </div>
        <div className="container-fluid list-jobs-wrapper">
            <div className="list-jobs-content container">
                <div className="title-lbc">
                    Danh sch tuyn dng mi nht
                </div>
                <div className="content-lbc">
                    {
                        !isLoading ? 
                        job.data !== undefined ?
                        job.data.length > 0 ? 
                        job.data.map((e, i) => {
                            return <NavLink key={e.id} to={{pathname: `/job-detail/${e.id}`, idItem: e.id}} className="link-item">
                            <div className="job-item">
                                <div className="title-job-item">
                                    <img src="images/logo_official.png" />
                                    <div className="company-name"></div>
                                </div>
                                <div className="right-content">
                                    <div className="desc">
                                        <div className="desc-item salary">Negotiative</div>
                                        <div className="desc-item major">{e.major.major_name}</div>
                                        <div className="desc-item type-work">{e.work_type}</div>
                                    </div>
                                    <div className="rc-content">
                                        <div className="title-text col-sm-6 p-0">{e.title}</div>
                                        <div className="content-button col-sm-6 justify-content-end p-0">
                                            <NavLink to={`/job-detail/${e.id}`} className="btn-t btn-link-app">APPLY</NavLink>
                                        </div>
                                    </div>
                                    <div className="time-left">{getTimeLeft(e.end_time)}</div>
                                </div>
                            </div>
                        </NavLink>
                        }) :
                        <div className="d-flex justify-content-center flex-row">
                            <div class="err">4</div>
                            <div className="f"><i class="far fa-question-circle fa-spin"></i></div>
                            <div class="err2">4</div>
                        </div>
                        : <div className="d-flex justify-content-center">
                            <div className="spinner-grow" role="status mr-3">
                                <span className="sr-only">Loading...</span>
                            </div>
                            <div className="spinner-grow" role="status mr-3">
                                <span className="sr-only">Loading...</span>
                            </div>
                            <div className="spinner-grow" role="status mr-3">
                                <span className="sr-only">Loading...</span>
                            </div>
                        </div> :
                        <div className="d-flex justify-content-center">
                            <div className="spinner-grow" role="status mr-3">
                                <span className="sr-only">Loading...</span>
                            </div>
                            <div className="spinner-grow" role="status mr-3">
                                <span className="sr-only">Loading...</span>
                            </div>
                            <div className="spinner-grow" role="status mr-3">
                                <span className="sr-only">Loading...</span>
                            </div>
                        </div>
                    }
                    {
                        job.data !== undefined ?
                        job.data.length > 0 ?
                        <div className="d-flex justify-content-end pr-3">
                            <nav aria-label="Page navigation example">
                                <ul className="pagination">
                                    {
                                        job.prev_page_url !== null ? 
                                        <li className="page-item"><a className="page-link" onClick={() => handleGetJob(job.prev_page_url)}>Previous</a></li>
                                        : <li className="page-item disabled"><NavLink className="page-link" aria-disabled="true" to="#">Previous</NavLink></li>
                                    }
                                    {
                                        job.next_page_url !== null ?
                                        <li className="page-item"><a className="page-link" onClick={() => handleGetJob(job.next_page_url)}>Next</a></li>
                                        : <li className="page-item disabled"><NavLink className="page-link" aria-disabled="true" to="#">Next</NavLink></li>
                                    }
                                    
                                </ul>
                            </nav>
                        </div>
                        : null : null
                    }
                    
                </div>
            </div>
        </div>
    </div>)
}

export default HomePage;